.skills {
   padding: 10px;
}
.card__info {
   box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
   border-radius: 25px;
   padding: 20px 30px;
   height: 100%;
   background-color: var(--background-color);
   color: var(--text-color);
   transition: background-color 250ms linear, color 250ms linear;
}
.card__header {
   text-align: center;
   font-size: 24px;
   padding-bottom: 20px;
}
.card__col {
   padding-left: 20px;
}
